<template>
  <div class="page">
	  <input type="text" v-model="search">
	 <button @click="goSearch" >搜索</button>
	  <div id="map" style="height: 600px"></div>	 
  </div>
</template>

<script>
	export default {
		name:"no-page",
		data(){
		  return {
		    search:''
		  }
		},
		methods:{
		  goSearch(){
		    this.map.clearOverlays();
		    var local =  new BMapGL.LocalSearch(this.map, {renderOptions: {map: this.map, autoViewport: false}});  
		    local.searchNearby(this.search,this.point,1000);
		    this.map.addOverlay(this.marker);
		  }
		},
		mounted(){
		  this.map = new BMapGL.Map('map'); // 创建Map实例
		  
		  this.point = new BMapGL.Point(121.49824642727945, 31.38456001767598);
		  
		  this.map.centerAndZoom(this.point, 15); // 初始化地图,设置中心点坐标和地图级别
		  
		  this.marker = new BMapGL.Marker(this.point);
		  
		  this.map.addOverlay(this.marker);
		  
		}
	}
</script>

<style>
	.page input{
		height:42px;
		width:80%;
	}
		
	.page button {
		height:42px;
		width:20%;
	}
</style>
