<template>
  <div class="detail paddingTop">
    
    <div class="detail__list">
      <uc-cell
        v-for="(item,index) of  column"
        :key="item._id"
        :data="item"
        :to="`/detail/${item._id}?collectionName=column`"
      />
    </div>  
  </div>
</template>

<script>
  import UcCell from '../components/cell/uc-cell.vue'
  import {mapState} from "vuex"
  export default {
    name:'column',
    components:{
      UcCell
    },
	computed:mapState("column",["column"]),
	mounted(){
		this.$store.dispatch("column/column")
	}
  }
 
</script>

