import axios from "../../plugins/axios.js"
let state={
	column:[],
};
let actions={
    column:({commit},payload)=>{		
		axios({
				  url:"/news/column",
				  params:{
					  _limit:16
				   },
		   }).then(res=>{
				   // this.column=res.data.data
			commit("column",res.data.data)
				})
		
	}		
};
let mutations={
	column:(state,payload)=>state.column=payload
};
export default {
   namespaced:true,  
   state,actions,mutations
}