import axios from "../../plugins/axios.js"
import {baseUrl} from "../../config";
let state={
	detail:{},
};
let actions={
    detail:({commit},{_id,collectionName})=>{
		axios.get(`/news/${collectionName}/${_id}`).then(res=>{
				   // this.detail=res.data.data			
				if(!res.data.data.detail.auth_icon.includes('http')){
				  res.data.data.detail.auth_icon = baseUrl + res.data.data.detail.auth_icon;
				}   
			    commit("detail",res.data.data)
				})
		
	}		
};
let mutations={
	detail:(state,payload)=>state.detail=payload
};
let getters={
	setDate(state){
			let  d = new Date();
			   d.setTime(state.detail.time);
			     let year = d.getFullYear();
				  		let month = d.getMonth() + 1;
				  		let date = d.getDate();			  		
				  		let hour = d.getHours();
				  		let min = d.getMinutes();
				  		let sec = d.getSeconds();
			let fillezero = n => n < 10 ? '0' + n : '' + n;
			 return `${year}年${fillezero(month)}月${fillezero(date)}日 ${fillezero(hour)}:${fillezero(min)}:${fillezero(sec      )}`		
	}
};

export default {
   namespaced:true,  
   state,actions,mutations,getters
}