import axios from "../../plugins/axios.js"
import {baseUrl} from "../../config"
let state={
	banner:[],
	home:[]
};
let actions={
    banner:({commit},payload)=>{
		axios({
				  url:"/news/user",
				  params:{
					  _limit:3
				   },
		   }).then(res=>{
				   // this.banner=res.data.data
				   if(!res.data.data)  return;
				   res.data.data.forEach((item,index)=>{					  
				     item.banner = baseUrl + item.icon;
				     item.title = "xxxx1";
				     item.sub_title =  "xxx3";
				   })
			     commit("banner",res.data.data)
				})
		
	}		,
	home:({commit},payload)=>{
		axios({
				  url:"/news/home",
				  params:{
					  _limit:30
				   },
		   }).then(res=>{
				   // this.banner=res.data.data
			commit("home",res.data.data)
				})
		
	}		
};
let mutations={
	banner:(state,payload)=>state.banner=payload,
	home:(state,payload)=>state.home=payload
};
export default {
   namespaced:true,  
   state,actions,mutations
}