<template>
	<div class="cell">
		<div @click="getDate">
			<h1><span v-show="index!==null">{{index+1}}.</span>{{data.title}}</h1>
			<p>{{data.des}}</p>			
		</div>
		<slot></slot>
	</div>
</template>

<script>
	export default {
	 name:'uc-cell',
	 props:{
	   data:{
		   type:Object,
		   required:true,
		   default:function(){
			  return {
				  title:'xxx',
				  des:'xxxxx'
			  } 
		   }
	   },
	   index:{
		   type:Number,
		   default:null,
		   
	   },
	   to:{
		  type:String,
		  default:"",
	   },
	   url:{
		   type:String,
		   default:"",
	   },	   
	 },
	 methods:{
		 getDate(){
		   if(this.url) {
			   window.open(this.url,"_blank")
		   }
		   if(this.to){
			 this.$router.push(this.to)
		   }
		 }
	 }
	}
</script>

<style>
  .cell { color:#494d4d; padding:0.2rem 0; border-bottom:1px dashed #ccc;}
  .cell h1{max-height:0.9rem; font-size:0.16rem; overflow:hidden;}
  .cell p{max-height:1.8rem; margin-top:0.05rem;overflow:hidden;font-size:0.16rem;};
  .cell{clear: both;/* margin-bottom:1rem; */}
  .slot{
    float: right;
  }	
</style>
