<template>
	<div class="banner">
		<ul class="clearfix">
		  <router-link 
		    tag="li"  v-for="(val,idx) of data"
			 :key="val._id"
			 :to="`/detail/${val._id}?collectionName=${collectionName}`"
			 >
		  <img :src="val.banner" alt=""/>
		    <div class="text-box">
		      <h2>{{val.title}}</h2>
		      <p>{{val.sub_title}}</p>
		    </div>
		  </router-link>
		</ul>
	</div>
</template>

<script>
 import $ from  "jquery"	
 import Swipe from "./js/swiper.js"
  export default {
	 name:"uc-banner",
	 props:{
		 data:{
			 type:[String,Object,Array],
		 },
		 collectionName:{
			 type:String,
		 },
		 autoplay:{
			 type:Number,
		 },		 
	 },
	mounted(){
	  this.initSwiper()
	} ,
	updated(){
	  this.initSwiper()
	},
	methods:{
		initSwiper(){
			// console.log(this.data);
		  new Swipe($('.banner')[0],{
		      auto:this.autoplay || 2000,
		      continuous:true,
		      stopPropation:true,
		      callback:function (index,element){
		      $('.banner ol li').removeClass('active');
		      $('.banner ol li').eq(index).addClass('active');
		    }
		  })
		 },
	},
	 
  }
</script>

<style scoped>
	.banner{position: relative;overflow: hidden; z-index: 1 ;}
	.banner ul li{width:6.4rem;float:left;position: relative;}
	.banner ul li img{width:100%; display:block; height:3.22rem}
	.banner ul li .text-box{width:100%; padding:0 0.3rem;height:1.24rem; position:absolute; left:0; bottom:0; color:#fff; background:rgba(0,0,0,0.5);}
	.text-box h2{ font-size:0.3rem; font-weight:normal; text-align:center;margin-top:0.22rem; line-height:100%; margin-bottom:0.22rem; overflow:hidden;white-space:nowrap;text-overflow:ellipsis;}
	.text-box p{ line-height:100%;text-align:center;}	
	/* .banner ol{position:absolute; right:0.2rem;bottom:0.2rem;}
	.banner ol li{width:0.15rem;height:0.15rem; background:#5477b2; float:left; border-radius:50%; margin-right:0.08rem;}
	.banner ol li.active{ background:#fff;} */
</style>
