<template>
	<div class="uc-field">
		<span v-if="label">{{label}}</span>
		<input :type="type" :value="value" @input="$emit('input',$event.target.value)">
	</div>
</template>

<script>
	export default {
	 name:"uc-field",
	 props:{
		value:{
			type:[String,Number],
			default:"",		
		},
		label:{
			type:String,
			default:"用户名",			
		},
		type:{
			type:String,
			default:"text"
		}
	 }
	}
</script>

