<template>

	<div id="app">
		<uc-loading v-show="this.$store.state.bLoading"></uc-loading>
		<app-header v-show="this.$store.state.bNav"></app-header>
		<router-view v-title  :data-title="$route.meta"></router-view>
		<app-footer  v-show="this.$store.state.bFooter"></app-footer>
	</div>
</template>

<script>
	import AppHeader from './header.vue'
	import AppFooter from './footer.vue'
	import UcLoading from '../components/loading/uc-loading.vue'
	

	export default {

		name: 'App',
		data() {
			return {}
		},
		components: {
			AppHeader,
			AppFooter,
			UcLoading
		},	
		mounted(){	
		},
		watch:{
		 $route:{
			 handler(nextValue){
				 let path= nextValue.path
				 if(/(home|column|follow)/.test(path)){
					// this.$root.bNav=this.$root.bFooter=true
					this.$store.commit("nav",true)
					this.$store.commit("foot",true)
				 } 
				 if(/(detail|reg|login)/.test(path)){
					// this.$root.bNav=this.$root.bFooter=false
					this.$store.commit("nav",false)
					this.$store.commit("foot",false)
				 } 
				 if(/(user|shopcar)/.test(path)){
					// this.$root.bNav=false
					// this.$root.bFooter=true
					this.$store.commit("nav",false)
					this.$store.commit("foot",true)
				 }
				 if(!(/(detail)/.test(path))){
					this.$store.commit("detail/detail",[])					 
				 }
			 },
			  immediate:true 			 
		 }
		}
		

	}
</script>
