import axios from "../../plugins/axios.js"
let state={
	follow:[],
};
let actions={
    follow:({commit},payload)=>{
		axios({
				  url:"/news/follow",
				  params:{
					  _limit:12
				   },
		   }).then(res=>{
				   // this.follow=res.data.data
			commit("follow",res.data.data)
				})
		
	}		
};
let mutations={
	follow:(state,payload)=>state.follow=payload
};
export default {
   namespaced:true,  
   state,actions,mutations
}