<template>
  <div class="home paddingTop" >
    <uc-banner
      :data="banner"
      collectionName='banner'
    />
    
    <div class="home__list">
      <uc-cell
        v-for="(item,index) of home"
         :key="item._id"
         :data="item"	
         :index="index"
         :to="`/detail/${item._id}?collectionName=home`"
      />
    </div>
    
    
    
  </div>
</template>

<script>
  import UcBanner from '../components/banner/uc-banner.vue'
  import UcCell from '../components/cell/uc-cell.vue'
  import {mapState} from "vuex"
  export default {
    name:"home",
    components:{
      UcBanner,UcCell
    },  
	computed:{
		...mapState("home",["home"]),
		...mapState("home",["banner"])
	},
    mounted(){ 
		this.$store.dispatch("home/home");
		this.$store.dispatch("home/banner")
    },
  }
</script>

<style scoped>
  .home__list{
    padding: 0 .2rem;
  }
</style>